import create from 'zustand'

const pageTitle = (set) => ({
    data: "ABOUT",
    setData: (data) => set(() => ({data: data}))
})

const color = (set) => ({
    data: {},
    setColor: (colors) => set(() => ({data: colors}))
})

const projects = (set) => ({
    data: {},
    setData: (data) => set(() => ({data: data}))
})

const projectNumber = (set) => ({
    data: 1,
    setData: (data) => set(() => ({data: data}))
})

const singleProject = (set) => ({
    data: {},
    setData: (data) => set(() => ({data: data}))
})

const link = (set) => ({
    data: "/",
    setData: (data) => set(() => ({data: data}))
})



export const useColorScheme = create(color)
export const usePageTitle = create(pageTitle)
export const useProjects = create(projects)
export const useProjectNumber = create(projectNumber)
export const useSingleProject = create(singleProject)
export const useLink = create(link)
