import { useColorScheme, useProjectNumber } from "../store/store";

const Dots = ({ index }) => {

    const setNumber =  useProjectNumber(state => state.setData)
    const number = useProjectNumber(state => state.data)
    const color = useColorScheme(state => state.data)

    const handleClick = (index) => {
       setNumber(index)
    }

    return ( 
        <div id={index === number ?  'dot-active' : 'dot'}  onClick={() => handleClick(index)}>
        </div>
     );
}
 
export default Dots;