import { useProjectNumber, useProjects, useSingleProject } from "../../store/store";
import Card from "../Card"
import Dots from '../Dots'
import { useSwipeable } from "react-swipeable";

const FeedItems = () => {
 
   const data = useProjects(state => state.data)
   const number = useProjectNumber(state => state.data)
   const setNumber = useProjectNumber(state => state.setData)
   const setSingleProject = useSingleProject(state => state.setData)

   const element = data.filter((x) => {
         return(x.fields.id === number)
   })

   setSingleProject(element[0].fields)

   const leftSwipe = () => {
      if(number <= (data.length - 2)){
         setNumber(number + 1)
      }else{
         setNumber(1)
      }
   }
   const rightSwipe = () => {
      if(number > 1){
         setNumber(number - 1)
      }else{
         setNumber(data.length - 1)
      }
   }
   const handlers = useSwipeable({
      onSwipedLeft: () => leftSwipe(),
      onSwipedRight: () => rightSwipe()
    });
    
    return ( 
        <div   {...handlers} className='feed-container'>
                  <Card />
                  <div id='dot-nav'>
                  {data.slice(0,5).reverse().map((x) => {
                     return(<Dots index={Number (x.fields.id)} />)
                  })}
                  </div>
        </div>
     );
}
 
export default FeedItems;