import { useEffect } from "react";
import { client } from "../../service/client";
import { useColorScheme, usePageTitle, useProjects } from "../../store/store";
import About from "../../Components/about"
import Feed from "../../Components/feed"
import Skills from "../../Components/skills"
import Contact from "../../Components/contact"
import Header from "./Header";

const Layout = () => {
    const color = useColorScheme(state => state.data)
    const setProjects = useProjects(state => state.setData)
    const page = usePageTitle(state => state.data)


    useEffect(() => {
     
       client.getEntries()
       .then((res) => {
          setProjects(res.items)
       })
       .catch(console.error)
    })
 
    
    const styleSheet = {
        bgColor: {
            background: color.primary
        }
    }

    return ( 
        <div className="layout" style={styleSheet.bgColor}>
            <Header /> 
            {page === 'ABOUT' ? <About /> : null}
            {page === 'SKILLS' ? <Skills /> : null}
            {page === 'FEED' ? <Feed /> : null}
            {page === 'CONTACT' ? <Contact /> : null}
            
        </div>
     );
}
 
export default Layout;