import About from './About'
import { useColorScheme, usePageTitle } from '../../store/store'
import { useSwipeable } from 'react-swipeable'
import image from '../blob.png'
function AboutPage() {
  const color = useColorScheme(state => state.data)
  const setPageTitle = usePageTitle(state => state.setData)

  const leftSwipe = () => {
    setPageTitle('SKILLS')
  }
  
  const leftRight = () => {
    setPageTitle('CONTACT')
  }

  const handlers = useSwipeable({
    onSwipedLeft: () => leftSwipe(),
    onSwipedRight: () => leftRight()
  });

  return (
    <div {...handlers} className="about-container" style={{background: color.secondary}}>
      <About />
      <svg width="2565" height="328" id='svg-blob' fill="none" xmlns="http://www.w3.org/2000/svg">
<path fill-rule="evenodd" clip-rule="evenodd" d="M0 161.988V327.019H2565V0C2548.86 7.52264 2531.43 14.8249 2512.62 21.8801C2334 88.8985 2097.77 89.9299 1854.27 90.993C1690.06 91.7099 1522.55 92.4412 1367.16 113.433C1233.12 131.541 1110.19 167.651 989.472 203.11C821.75 252.376 658.307 300.386 475.284 297.111C249.149 293.064 123.329 228.061 0 161.988Z" fill="#3BB873"/>
</svg>
    </div>
  )
}

export default AboutPage;