import { useEffect, useRef, useState } from "react";
import { useColorScheme, useSingleProject } from "../store/store";
import lottie from "lottie-web";
import link from './link.json'
import axios from 'axios'

const Card = () => {

    const color = useColorScheme(state => state.data);
    const [toggle, setToggle] = useState(true)
    const container = useRef(null);
    const data = useSingleProject(state => state.data)
 
    useEffect(() => {
        lottie.loadAnimation({
          container: container.current,
          renderer: 'svg',
          loop: true,
          autoplay: true,
          animationData: link
        })
      },[])



    return (   
     <div className='card-body'>
         <div className='card-element' style={{color: color.font}}> 
         <img src={data.image.fields.file.url} alt='project'/>
         <h2> { data.title } </h2> 
         </div>
         <div className={toggle ? "overlay" : "overlay-active"} style={{background: color.primary}} onClick={() => setToggle(!toggle)}>
             {toggle ? <h5>Show more</h5> : 
             <div className='overlay-body'>
              <h2>{data.title}</h2>
              <p> <span> Description: </span>{data.description}</p>
              <p> <span> Technology used: </span>{data.technology}</p>
              <p href={data.link}> <span> Link: </span>{data.link}</p>
              <div ref={container} className='link-animation'></div>
            </div>
             }
         </div>
     </div>
     );
}
 
export default Card;