
import { useColorScheme, usePageTitle } from '../../store/store';
import { Color } from '../Color';

const Header = () => {
    const colorData = useColorScheme(state => state.data)
    const title = usePageTitle(state => state.data);
    const setTittle = usePageTitle(state => state.setData)
    const setColor = useColorScheme(state => state.setColor)

        switch(title){
            case "ABOUT": 
            setColor(Color.ABOUT)
            break;
            case "SKILLS": 
            setColor(Color.SKILLS)
            break;
            case "FEED": 
            setColor(Color.FEED)
            break;
            case "CONTACT": 
            setColor(Color.CONTACT)
            break;
        }

    const styleSheet = {
        bgText: {
            color: colorData.font
        },
        bgOpacity: {
            background: colorData.secondary,
            color: "#000"
        }
    }
    
    return ( 
        <div className='header'>
           <div id='bg-div'>
               <h1 id='bg-text' style={styleSheet.bgText}>
                   {title}
               </h1>
           </div>
           <ul>
               <li style={title === "ABOUT" ? styleSheet.bgOpacity : null} onClick={() => setTittle("ABOUT")}>ABOUT</li>
               <li style={title === "SKILLS" ? styleSheet.bgOpacity : null} onClick={() => setTittle("SKILLS")}>SKILLS</li>
               <li style={title === "FEED" ? styleSheet.bgOpacity : null} onClick={() => setTittle("FEED")}>FEED</li> 
               <li style={title === "CONTACT" ? styleSheet.bgOpacity : null} onClick={() => setTittle("CONTACT")}>CONTACT</li> 
           </ul>
        </div>
     );

   
}


 
export default Header;